@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
.gg-arrow-long-up,.gg-arrow-long-up::after{display:block;box-sizing:border-box;width:6px}.gg-arrow-long-up{position:relative;-webkit-transform:scale(1);transform:scale(1);-webkit-transform:scale(var(--ggs, 1));transform:scale(var(--ggs, 1));border-right:2px solid transparent;border-left:2px solid transparent;box-shadow:inset 0 0 0 2px;height:24px}.gg-arrow-long-up::after{content:"";position:absolute;height:6px;border-top:2px solid;border-left:2px solid;-webkit-transform:rotate(45deg);transform:rotate(45deg);top:0;left:-2px}

*{box-sizing:border-box;padding:0;margin:0}.bottom{display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end;-webkit-align-items:center;align-items:center;grid-gap:0.3rem;gap:0.3rem;color:#ffaf40;margin-top:0.5rem}.arrow{height:0.8rem}.confirmed{color:#7d5fff}.recovered{color:#32ff7e}.deaths{color:#ff3838}.active{color:#7bed9f}.critical{color:#ff3838}:root{--bg-primary: #222529;--bg-secondary: #282b2e;--text-primary: #fff;--text-secondary: #ccc;--border: 1px solid rgb(87, 87, 87)}body{font-family:"Nunito", sans-serif}.app{display:grid;height:100vh;width:100vw;grid-template-columns:1.6fr 1fr}.body{display:grid;height:100vh;grid-template-rows:0.4fr 0.9fr 4fr;grid-gap:1rem;gap:1rem;background-color:#282b2e;background-color:var(--bg-secondary);padding:1rem}.body-header{display:-webkit-flex;display:flex;height:100%;-webkit-align-items:center;align-items:center;color:#fff;color:var(--text-primary);font-size:2.3rem}.body-cards{display:grid;grid-template-columns:repeat(auto-fit, minmax(200px, 1fr));color:#fff;color:var(--text-primary);grid-gap:1rem;gap:1rem}.card{display:grid;-webkit-align-items:center;align-items:center;border:1px solid rgb(87, 87, 87);border:var(--border);border-radius:9px;padding:0.3rem 1.2rem}.card div{text-align:right}.card h2{font-size:1.6rem}.map-container{height:100%;width:100%}.sidebar{display:grid;padding:0rem 1rem;height:100vh;grid-template-rows:1fr 9fr;background-color:#222529;background-color:var(--bg-primary);color:#fff;color:var(--text-primary)}.sidebar-header{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;background-color:#222529;background-color:var(--bg-primary);-webkit-justify-content:space-between;justify-content:space-between;position:-webkit-sticky;position:sticky;top:0}.sidebar-header input{border:1px solid rgb(87, 87, 87);border:var(--border);padding:0.7rem;border-radius:9px;outline:none;background-color:#222529;background-color:var(--bg-primary);color:#fff;color:var(--text-primary)}.sidebar-body{overflow:scroll;overflow-x:hidden;scrollbar-width:none}.sidebar-body-header{display:grid;grid-template-columns:repeat(6, 1fr);-webkit-align-items:center;align-items:center;text-align:center;text-align:left}.sidebar-body-header h3{text-align:right}.sidebar-body-country{display:grid;grid-template-columns:repeat(6, 1fr);-webkit-align-items:center;align-items:center;text-align:center;padding:0.9rem;text-align:left;background-color:#4b4b4b;margin:1rem 0rem;border-radius:9px}.sidebar-body-country h5,.sidebar-body-country h4{text-align:right}.sidebar-body-country h4{font-size:1rem}.sidebar-body-country h5{font-size:0.8rem}.sidebar-body-country h3{font-size:1.2rem}

